.ButtonBar{
  display: flex;
  flex-direction: row;
  align-items: right;
  justify-content: right;
  padding-right: 1rem;
  gap:1%;
  
}

.ButtonDiv{
  height: 1%;
}

.ButtonLink{
  display: inline-block;
  width: fit-content;
  height: fit-content;
}

.icon{
  height:100%;
  width: 100%;
  display: block;
}

.Name{
  font-size: calc(10px + 5vmin);  
  font-weight: 700;
  margin: 2%;
  text-align: left;
  justify-content: left;
  justify-content: left;
}

.Intro{
  font-size: calc(2px + 2vmin);
  color: #8fa8c6;
  letter-spacing: 1px;
  font-weight: 500;
  text-align: left;
  margin: 2%;
}

.NavBar{
  display: flex;
  flex-direction: column;
  text-align: left;
  /* padding-left: 2%; */
  font-size: calc(2px + 2vmin);
  color:#FFFFFF;
  margin: 2%;
}

.Nav{
  padding-block: 2%;
  list-style-type: none;   
  padding-inline: 5px;
  max-width:40%;
  border-top: 1px solid #C17C74;
}

.Nav:hover{
  color: #C17C74;
}

@media (max-width:650px){
  .ButtonBar{
    margin-top:1%;
  }

  .Intro{
    font-size:14px;
  }

  .NavBar{
    font-size:14px;
    flex-direction: row;
    justify-content: center;
  }

  .Nav{
    list-style-type: none;
    text-align: center;
    padding-inline: 5px;
    min-width:110px;
    border: 1px solid #C17C74;
  }
}