
.Screen{
  max-height: 100vh;
  max-width:100vw;
  background-color: #131a2c;
  color:#FFFFFF;
  padding:1%;
  display: flex;
  flex-direction: column;
}

.row {
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: calc(10px + 2vmin);
  height: 80vh;
  padding-inline: 5%;
  gap:5%;
}

.leftCol {
  display: flex;
  flex-direction: column;
  align-items: left;
  font-size: calc(10px + 2vmin);
  width:50%;
}

.rightCol{
  display: flex;
  justify-content: left;
  width:50%;
}

@media (max-width: 768px) {
  .Screen{
    width:100%;
    overflow: hidden;
  }
}

@media (max-width: 650px) {
  .Screen{
    min-height: 90vh;
    width:100%;
    padding:1%;
  }
  .row { 
    flex-direction: column;
  }
  .leftCol {
    display: flex;
    flex-direction: column;
    align-items: left;
    font-size: calc(10px + 2vmin);
    width:100%;
  }
  
  .rightCol{
    display: flex;
    justify-content: left;
    width:100%;
    align-items: center;
  }
}
